.container {
  position: relative;
  padding: 8px 16px;
}

.dropdown__button {
  display: flex;
  padding: var(--button_medium);
  border: none;
  border-radius: 8px;
  transition: all 0.1s ease-in;
  font-family: VelaSans-Medium;
  font-size: 16px;
  line-height: 100%;
}

.dropdown__button_not_acive {
  background-color: #fff;
}

.dropdown__button_not_acive:hover {
  background-color: #f4f7fa;
}

.dropdown__button_active {
  background-color: #000;
  color: #fff;
}

.arrow {
  align-self: center;
  width: 10px;
  height: 6px;
  margin-left: 6px;
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='10' height='6' viewBox='0 0 10 6' fill='none' xmlns='http://www.w3.org/2000/svg' %3e%3cpath fillRule='evenodd' clipRule='evenodd' d='M0.528758 0.528575C0.268409 0.788925 0.268409 1.21103 0.528758 1.47138L4.52876 5.47138C4.78911 5.73173 5.21122 5.73173 5.47157 5.47138L9.47157 1.47138C9.73192 1.21104 9.73192 0.788925 9.47157 0.528576C9.21122 0.268226 8.78911 0.268226 8.52876 0.528576L5.00016 4.05717L1.47157 0.528575C1.21122 0.268226 0.789108 0.268226 0.528758 0.528575Z' fill='black' /%3e%3c/svg%3e");
  transition: all 0.3s ease;
}

.arrow_acive {
  transform: rotate(180deg);
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='10' height='6' viewBox='0 0 10 6' fill='none' xmlns='http://www.w3.org/2000/svg' %3e%3cpath fillRule='evenodd' clipRule='evenodd' d='M0.528758 0.528575C0.268409 0.788925 0.268409 1.21103 0.528758 1.47138L4.52876 5.47138C4.78911 5.73173 5.21122 5.73173 5.47157 5.47138L9.47157 1.47138C9.73192 1.21104 9.73192 0.788925 9.47157 0.528576C9.21122 0.268226 8.78911 0.268226 8.52876 0.528576L5.00016 4.05717L1.47157 0.528575C1.21122 0.268226 0.789108 0.268226 0.528758 0.528575Z' fill='white' /%3e%3c/svg%3e");
}

.dropdown__menu {
  position: absolute;
  z-index: 1000;
  transition: opacity 1s ease;
  background: #ffffff;
  padding: 12px;
  margin-top: 10px;
  box-shadow: 0px 4px 16px rgba(0, 0, 0, 0.16);
  border-radius: 8px;
  display: none;
}

.dropdown__menu_deactive {
  opacity: 0;
}

.dropdown__menu_active {
  display: block;
  opacity: 1;
}

.link {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 8px;
  padding: 8px 16px 8px 8px;
  cursor: pointer;
  background-color: #fff;
  transition: background-color 0.1s ease-in;
  border-radius: 8px;
}

.link:hover {
  background-color: #f4f7fa;
}

.link:hover .checkbox {
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg' %3e%3cpath fillRule='evenodd' clipRule='evenodd' d='M13.1324 4.18971C13.3958 4.44695 13.4008 4.86903 13.1436 5.13245L6.63318 11.7991C6.502 11.9334 6.32025 12.0062 6.13261 11.9996C5.94496 11.9929 5.76882 11.9075 5.64748 11.7642L2.82456 8.43084C2.58662 8.14987 2.62149 7.7292 2.90246 7.49126C3.18344 7.25331 3.6041 7.28819 3.84205 7.56916L6.19132 10.3432L12.1897 4.20088C12.4469 3.93747 12.869 3.93246 13.1324 4.18971Z' fill='black' /%3e%3c/svg%3e");
}

.checkbox_input {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
}

.checkbox {
  width: 20px;
  height: 20px;
  border-radius: 6px;
  background-color: #e7ecf1;
  background-position: center;
}

.checkbox_input:checked + .checkbox {
  background-color: #000;
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg' %3e%3cpath fillRule='evenodd' clipRule='evenodd' d='M13.1324 4.18971C13.3958 4.44695 13.4008 4.86903 13.1436 5.13245L6.63318 11.7991C6.502 11.9334 6.32025 12.0062 6.13261 11.9996C5.94496 11.9929 5.76882 11.9075 5.64748 11.7642L2.82456 8.43084C2.58662 8.14987 2.62149 7.7292 2.90246 7.49126C3.18344 7.25331 3.6041 7.28819 3.84205 7.56916L6.19132 10.3432L12.1897 4.20088C12.4469 3.93747 12.869 3.93246 13.1324 4.18971Z' fill='white' /%3e%3c/svg%3e");
}
